import React from 'react';
import "./endzone-button.css"

class EndzoneButton extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        return(
            <button onClick = {this.props.onClick} className='endzone-btn' style = {this.props.style} type="button">
                <div className='btn-body'>
                    <div className='btn-text'>
                        <p>{this.props.text}</p>
                    </div>
                    <div className='btn-icon'>
                        {this.props.icon}
                    </div>                    
                </div>
            </button>
        );
    }
}



export default EndzoneButton;