import React from 'react';
import "./coaches-hub.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import SideNav from '../../../components/navigators/endzone-side-nav/side-nav';
import ToolFolder from '../../../components/tools/endzone-tool-folder/tool-folder';
import Footer from "../../../components/misc-items/footer/footer";
import StorageIcon from '@mui/icons-material/Storage';
import PostAddIcon from '@mui/icons-material/PostAdd';
import ScreenSearchDesktopIcon from '@mui/icons-material/ScreenSearchDesktop';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import MovieIcon from '@mui/icons-material/Movie';
import UploadFileIcon from '@mui/icons-material/UploadFile';
import UploadIcon from '@mui/icons-material/Upload';

class CoachesHub extends React.Component {
    constructor (props) {
        super(props);
        this.search_data = [
            {name: "Manage Formations", tags: ["Home", "Main Menu", "Endzone"], link: "/endzone/coaches-corner/formation"},
            {name: "Playbook", tags: ["Data", "Manage", "Management"], link: "/endzone/coaches-corner/playbook"},
            {name: "Squad Stats", tags: [], link: "/endzone/coaches-corner/stats/squad"},
            {name: "Individual Stats", tags: [], link: "/endzone/coaches-corner/stats/player"},
            {name: "Grade Players", tags: [], link: "/endzone/coaches-corner/grade"},
            {name: "Manage Roster", tags: [], link: "/endzone/coaches-corner/roster"}
        ];
    }


    render() {
        return(
            <div className="data-hub-page">
            <EndzoneNav links={[{title: "Return to Hub", link: "/endzone/hub"},]}></EndzoneNav>

            <div className="hub-body">
                
                <SideNav search_data = {this.search_data} links = {this.search_data}></SideNav>
                <div className='hub-container'>
                    <div className='hub-top-div'>
                        <div className='hub-top'>
                            <h2>Coaches Corner</h2>
                        </div>
                    </div>
                    <div className="hub-menu">
                        <div className="hub-left">
                            <ToolFolder folder_name = "Strategy"
                                tools = {
                                    [
                                        {"tool_name": "Manage Formations", "tool_text": "Create and manage formations that are used in games.", "tool_link": "/endzone/coaches-corner/formation", "icon": (<PostAddIcon sx = {{"font-size": '50px'}} className='tool-icon' ></PostAddIcon>)},
                                        {"tool_name": "Playbook", "tool_text": "Create and manage your playbook", "tool_link": "/endzone/coaches-corner/playbook", "icon": (<StorageIcon sx = {{"font-size": '50px'}} className='tool-icon' ></StorageIcon>)}
                                    ]
                                }>
                            </ToolFolder>
                            

                        </div>
                        <div className="hub-right">
                            <ToolFolder folder_name = "Stats" 
                                tools = {
                                            [
                                                {"tool_name": "Squad Stats", "tool_text": "View your squad's stats", "tool_link": "/endzone/coaches-corner/stats/squad", "icon": (<AnalyticsIcon sx = {{"font-size": '50px'}} className='tool-icon' ></AnalyticsIcon>)},
                                                {"tool_name": "Individual Stats", "tool_text": "View individual stats", "tool_link": "/endzone/coaches-corner/stats/player", "icon": (<MovieIcon sx = {{"font-size": '50px'}} className='tool-icon' ></MovieIcon>)}
                                            ]
                                        }>
                            </ToolFolder>
                        </div>
                    </div>
                    <div className='hub-bottom-div'>
                        <div className='hub-bottom'>
                            <ToolFolder folder_name = "Squad Management" 
                                tools = {
                                    [
                                        {"tool_name": "Grade Players", "tool_text": "Grade your players", "tool_link": "/endzone/coaches-corner/grade", "icon": (<UploadFileIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UploadFileIcon>)},
                                        {"tool_name": "Manage Roster", "tool_text": "Manage your squad's roster by adding more information", "tool_link": "/endzone/coaches-corner/roster", "icon": (<UploadIcon sx = {{"font-size": '50px'}} className='tool-icon' ></UploadIcon>)}
                                    ]
                                }>
                            </ToolFolder>
                        </div>
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </div>
        );
    }
}



export default CoachesHub;