import React from 'react';
import "./side-nav.css"
import EndzoneSearchBar from '../../search-bars/endzone-search-bar/endzone-search-bar';


class SideNav extends React.Component {
    constructor (props) {
        super(props);
    }
    

    render() {
        return(
            <div className='side-nav'>
                <div className='searchDiv'>
                    <EndzoneSearchBar data = {this.props.search_data}>
                        
                    </EndzoneSearchBar>
                </div>
                <div className='links-div'>
                    <ul>
                        {this.props.links ? this.props.links.map(
                            (item, index) => 
                                <li>
                                    <a href = {item.link}>{item.name}</a>
                                </li>
                            ) : ''
                        }
                    </ul>
                </div>
            </div>
        );
    }
}



export default SideNav;