import React from 'react';
import "./endzone-search-bar.css"

class EndzoneSearchBar extends React.Component {
    constructor (props) {
        super(props);
    }

    Similarity = (s1, s2) => {
        var longer = s1;
        var shorter = s2;
        if (s1.length < s2.length) {
          longer = s2;
          shorter = s1;
        }
        var longerLength = longer.length;
        if (longerLength == 0) {
          return 1.0;
        }
        return (longerLength - this.editDistance(longer, shorter)) / parseFloat(longerLength);
      }
    
    editDistance = (s1, s2) => {
        s1 = s1.toLowerCase();
        s2 = s2.toLowerCase();
      
        var costs = new Array();
        for (var i = 0; i <= s1.length; i++) {
          var lastValue = i;
          for (var j = 0; j <= s2.length; j++) {
            if (i == 0)
              costs[j] = j;
            else {
              if (j > 0) {
                var newValue = costs[j - 1];
                if (s1.charAt(i - 1) != s2.charAt(j - 1))
                  newValue = Math.min(Math.min(newValue, lastValue),
                    costs[j]) + 1;
                costs[j - 1] = lastValue;
                lastValue = newValue;
              }
            }
          }
          if (i > 0)
            costs[s2.length] = lastValue;
        }
        return costs[s2.length];
      }

    activateSearch = () => {
        document.getElementById("search-results").style.display = 'block'
    }

    deactivateSearch = () => {
        document.getElementById("search-results").style.display = 'none'
    }

    userInput = (e) => {
        var input = document.getElementById("search-entry").value
        input = input.toLowerCase();
        let matched_values = this.searchData(input);
        if (matched_values)
        {
            this.displaySearch(matched_values)
        }

    }

    searchData = (input) => {
        var data = JSON.parse(JSON.stringify(this.data));
        var matched_values = []
        if(input.length <= 3) 
        {
            return
        }
        else
        {
            for(let i = 0; i < data.length; i++)
            {
                if(this.Similarity(input.toLowerCase(), data[i].name.toLowerCase()) > .5)
                {
                    matched_values.push([data[i].name, data[i].link]);
                }
                else 
                {
                    for(let j = 0; j < data[i].tags.length; j++)
                    {
                        if(this.Similarity(input.toLowerCase(), data[i].tags[j].toLowerCase()) > .25)
                        {
                            matched_values.push([data[i].name, data[i].link]);
                            break;
                        }

                    }
                }
                if(matched_values.length == 5)
                {
                    return matched_values;
                }
            }
            return matched_values
        }
    }

    displaySearch = (matched_values) => {
        const div = document.getElementById("search-results");
        while (div.firstChild) {
            div.removeChild(div.lastChild);
        }
        for(let i = 0; i < matched_values.length; i++)
        {
            let a = document.createElement('a')
            a.innerText = matched_values[i][0]
            a.href = matched_values[i][1]
            div.appendChild(a);
        }

    }

    data = this.props.data;

    render() {
        return(
            <div className='endzone-search-bar' onMouseEnter={this.activateSearch} onMouseLeave={this.deactivateSearch}>
                <input id = "search-entry" className = "search-entry" type="text" placeholder='Search Endzone' onInput={this.userInput} style = {this.props.style}></input>
                <div className = "search-results" id = "search-results">
                </div>
            </div>
        );
    }
}



export default EndzoneSearchBar;