import React, { useState } from 'react';

import "./press-box-field.css"

import 'leaflet/dist/leaflet.css'
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import fieldLayer from './image/FootballField.png'

class PressBoxField extends React.Component {
  constructor (props) {
      super(props);
      this.state = {
        offense_direction: this.props.offense_direction
      };
      this.mapObject = Object(
        {
          mapBounds: L.latLngBounds([[0, 0], [.7, 1.25]]),
        }
      );
      this.icons = Object(
        {
          blueIcon: new L.Icon({
            iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png',
            shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
            iconSize: [25, 41],
            iconAnchor: [12, 41],
            popupAnchor: [1, -34],
            shadowSize: [41, 41]
          }),
          redIcon: new L.Icon({
            iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png',
            shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
            iconSize: [25, 41],
            iconAnchor: [12, 41],
            popupAnchor: [1, -34],
            shadowSize: [41, 41]
          }),
          greenIcon: new L.Icon({
            iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png',
            shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
            iconSize: [25, 41],
            iconAnchor: [12, 41],
            popupAnchor: [1, -34],
            shadowSize: [41, 41]
          }),
          yellowIcon: new L.Icon({
            iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png',
            shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
            iconSize: [25, 41],
            iconAnchor: [12, 41],
            popupAnchor: [1, -34],
            shadowSize: [41, 41]
          })
        }
      );
  }


  componentDidMount() {
    this.mapObject.map = L.map('map', {  
      zoomControl: false,
      maxBounds: this.mapObject.mapBounds,
      maxBoundsViscosity: 1.0,
      minZoom: 9,
      maxZoom: 11,
    }
    );
    this.mapObject.playStart = null;
    this.mapObject.playStart_Added = false;
    this.mapObject.playEnd = null
    this.mapObject.playEnd_Added = false;
    this.mapObject.passToggle = false
    this.mapObject.pass = null

    this.mapObject.driveBox = L.rectangle([[ 4, 4 ], [ 4, 4 ]], {color: "#2980b9", weight: 5, fill: true, fillColor: "#2980b9", fillOpacity: 0.75}).addTo(this.mapObject.map);
    this.mapObject.positiveBox = L.rectangle([[ 4, 4 ], [ 4, 4 ]], {color: "#05c46b", weight: 5, opacity: 1, fill: true, fillColor: "#05c46b", fillOpacity: 0.5}).addTo(this.mapObject.map);
    this.mapObject.negativeBox = L.rectangle([[ 4, 4 ], [ 4, 4 ]], {color: "#ff3f34", weight: 5, fill: true, fillColor: "#ff3f34", fillOpacity: 0.5}).addTo(this.mapObject.map);
    
    this.mapObject.map.fitBounds(this.mapObject.mapBounds);
    this.mapObject.map.setZoom(10);
    this.mapObject.map.on('click', (e) => this.mapClick(e));
    
    // Lock page on load
    this.props.sendMapLock(0)
    this.addLayers()
  }

  addLayers() {
    L.imageOverlay(fieldLayer, L.latLngBounds([[0, 0], [.7, 1.25]])).addTo(this.mapObject.map)
  }

  mapClick(e) {
    
    if(this.mapObject.passToggle && this.mapObject.playStart_Added && this.mapObject.playEnd_Added)
    {
      try {this.mapObject.map.removeLayer(this.mapObject.pass);}catch{}
      this.mapObject.pass = L.marker(e.latlng, {icon: this.icons.yellowIcon}).addTo(this.mapObject.map);
      this.props.sendPassPos(e.latlng.lat, e.latlng.lng)
      this.props.sendMapLock(4)
      return;
    }
    // If no marker has been added
    else if(!this.mapObject.playStart_Added)
    {
      this.mapObject.playStart = L.marker(e.latlng, {icon: this.icons.blueIcon}).addTo(this.mapObject.map);
      this.mapObject.playStart_Added = true;
      this.adjustDriveBox(e.latlng.lng)
      this.props.sendMapLock(1)
      this.props.sendFieldPos(e.latlng.lat, e.latlng.lng)
    }
    else {
      // If no play ending has been added
      if(!this.mapObject.playEnd_Added)
      {
        this.mapObject.playEnd = L.marker(e.latlng, {icon: this.icons.redIcon}).addTo(this.mapObject.map);
        this.updatePlayResultBox()
        this.adjustDriveBox(e.latlng.lng)
        this.mapObject.playEnd_Added = true;


        this.props.sendResultPos(e.latlng.lat, e.latlng.lng)
        this.props.sendMapLock(3)
      }
      else {
        // Play has ended reset behavior
        this.mapObject.map.removeLayer(this.mapObject.playStart);
        this.mapObject.playStart = L.marker(this.mapObject.playEnd.getLatLng(), {icon: this.icons.blueIcon}).addTo(this.mapObject.map);
        this.mapObject.playStart_Added = true;

        this.mapObject.playEnd = this.mapObject.map.removeLayer(this.mapObject.playEnd);
        this.mapObject.playEnd = null;
        this.mapObject.playEnd_Added = false;
        
        this.props.sendFieldPos(e.latlng.lat, e.latlng.lng)        
        this.props.sendMapLock(0)
        this.revertPlayResultBox()
      }

    }
  }
  
  simulateClick = (lat, lon) => {
    this.mapObject.map.fireEvent('click', {latlng: L.latLng(lat, lon)})
  }

  clearMap = () => {
    try {this.mapObject.map.removeLayer(this.mapObject.playEnd);}catch{}
    try {this.mapObject.map.removeLayer(this.mapObject.playStart);}catch{}
    try {this.mapObject.positiveBox.setBounds([[4, 4], [4, 4]])}catch{}
    try {this.mapObject.negativeBox.setBounds([[4, 4], [4, 4]])}catch{}
    try {this.mapObject.driveBox.setBounds([[4, 4], [4, 4]])}catch{}
    try {this.mapObject.map.removeLayer(this.mapObject.pass);}catch{}
    
    this.mapObject.playStart = null;
    this.mapObject.playEnd = null
    this.mapObject.playStart_Added = false;
    this.mapObject.playEnd_Added = false;
    this.props.sendMapLock(0)
  }

  togglePassPlacement = (toggle) => {
    this.mapObject.passToggle = toggle
    if(toggle){
      this.props.sendMapLock(2)
    } 

  }

  revertPlayResultBox() {
    this.mapObject.negativeBox.setBounds([[4, 4], [4, 4]])
    this.mapObject.positiveBox.setBounds([[4, 4], [4, 4]])
  }

  updatePlayResultBox(){
    let play_x = this.mapObject.playStart.getLatLng().lng
    let result_x = this.mapObject.playEnd.getLatLng().lng
    let resulting_yardage = play_x - result_x;
    if(this.props.offense_direction == "left")
    {
      // Positive Play
      if(resulting_yardage > 0)
      {
        this.mapObject.positiveBox.setBounds([[0.03, play_x], [.67, result_x]])
        this.mapObject.negativeBox.setBounds([[4, 4], [4, 4]])
      }
      // Negative Play
      else {
        this.mapObject.positiveBox.setBounds([[0.03, play_x], [.67, result_x]])
        this.mapObject.negativeBox.setBounds([[0.03, play_x], [.67, result_x]])
      }
    }
    else if(this.props.offense_direction == "right")
    {
      // Negative Play
      if(resulting_yardage > 0)
      {
        this.mapObject.positiveBox.setBounds([[4, 4], [4, 4]])
        this.mapObject.negativeBox.setBounds([[0.03, play_x], [.67, result_x]])
      }
      // Positive Play
      else {
        this.mapObject.positiveBox.setBounds([[0.03, play_x], [.67, result_x]])
        this.mapObject.negativeBox.setBounds([[4, 4], [4, 4]])
      }
    }

  }

  adjustDriveBox(lon)
  {
      var o_direction = this.props.offense_direction;
      if(o_direction == "right")
      {
        this.mapObject.driveBox.setBounds([[0.03, 0.02], [.67, lon]])
      }
      else if(o_direction == "left")
      {
        this.mapObject.driveBox.setBounds([[0.03, lon], [.67, 1.22]])
      }
  }

  componentWillUnmount() {
    this.mapObject.map.off()
    this.mapObject.map.remove()
  }

  buildMap = () => {
    this.mapObject.map = L.map('map', {  
      zoomControl: false,
      maxBounds: L.latLngBounds([
        [-2, 0],
        [12, 10]
      ]),
      maxBoundsViscosity: 1.0,
      minZoom: 6,
      maxZoom: 11});
  }

  render() {
      return(
          <div id = "map" onClick={() => this.buildMap} className="map-div">
          </div>
      );
  }
}


export default PressBoxField;