import React from 'react';
import "./tool-folder.css"
import ToolLink from '../endzone-tool-link/endzone-tool-link';
class ToolFolder extends React.Component {
    constructor (props) {
        super(props);
    }


    toolGroupClick = () => {
        if(this.props.link)
        {
            window.location.href = this.props.link;
        }
    }

    render() {
        return(
            <div className='tool-folder' id = "tool-folder" onClick={this.toolGroupClick}>
                <div className='tool-folder-heading'>
                    <h3>{this.props.folder_name}</h3>
                </div>
                <div className='tool-folder-body' id = 'tool-folder-body'>
                    
                    {this.props.tools ? this.props.tools.map(
                        (item, index) => 
                        <ToolLink tool_name = {item.tool_name} tool_text = {item.tool_text} tool_link = {item.tool_link} icon = {item.icon}></ToolLink>
                        ) : ''
                    }
                </div>
            </div>
        );
    }
}



export default ToolFolder;