import React from 'react';
import './EndzoneNav.css';
import logo from "./static/EndzoneMainLogo.png"

import axios from 'axios';

class EndzoneNav extends React.Component {
  constructor (props) {
        super(props);
        this.state = {
          currentCoach: "Coach Unknown"
        }
    }



  logoClick = () => {
    window.location.href = "/endzone/hub"
  }

  getProfile = () => {
    axios.get('/endzone/account/user/profile').then((response) => {
      this.setState({currentCoach: "Coach " + response.data.last_name})
    }, (reject) => {
      console.log(reject)
    })
  }

  componentDidMount = () => {
    this.getProfile()
  }

  render() {
    return (
      <div>
        <div className="endzone-nav">
          <div className='logo-container'>
            <img src={logo} className="logo" alt='Endzone Logo' onClick={this.logoClick}></img>
          </div>
          <div className='nav-links'>
            <ul>
              {this.props.links.map(
                  (item, index) => <li>
                              <a href = {item.link} className={item.className}>{item.title}</a>
                            </li>
                  )
              }
            </ul>
          </div>
          <div class="user-div">
              <button>
                  {this.state.currentCoach}
                  <i class="fa fa-caret-down"></i>
              </button>
              <div class="dropdown-content">
                  <a href="/endzone/account/profile">Profile</a>
                  <a href="/endzone/org/home">Org Management</a>
                  <a href="/endzone/team/home">Team Management</a>
                  <a href="/endzone/account/profile?action=changeTeam">Change Squad</a>
                  <a href="/logout">Log Out</a>
              </div>
          </div>
        </div>
      </div>
    );
  }
}

export default EndzoneNav;