import React from 'react';
import "./footer.css"

class Footer extends React.Component {
    copyrighter = () => {
        let cpr = document.getElementById("copyright");
        cpr.innerHTML = "&copy; 2023 - "+new Date().getFullYear()+" endzoneanalytics.com - All Rights Reserved.";
    };

    componentDidMount = () =>
    {
        this.copyrighter()
    }

    render() {
        return(
            <div className='footer'>
                <div className='center-content'>
                    <div className='navigation-links'>

                    </div>
                    <div className = "linkDiv">
                        <div id="copyright" align="center" className='copyright-text'>&copy; 2023 - 2023 endzone-analytics.com</div>
                    </div>
                </div>
            </div>
        )    
    }
}

export default Footer;