import React from 'react';
import "./endzone-tool-link.css"

class ToolLink extends React.Component {
    constructor (props) {
        super(props);
    }

    ToolLinkClick = () => {
        window.location.href = this.props.tool_link;
        console.log(this.props.tool_link)
    }

    render() {
        return(
            <div class = "tool-link-div" onClick={this.ToolLinkClick}>
                <h3 className='tool-name'>{this.props.tool_name}</h3>
                <div className='tool-icon-div'>
                    {this.props.icon}
                </div>
                <h2 className='tool-text'>
                    {this.props.tool_text}
                </h2>
            </div>
        );
    }
}



export default ToolLink;